@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
.authInput {
  width: 320px;
}

.authContainer {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  align-items: center;
}

.authBtns {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 320px;
}

.authBtn {
  cursor: pointer;
  border-radius: 20px;
  border: none;
  padding: 10px 0px;
  font-size: 12px;
  text-align: center;
  color: #fff;
  width: 150px;
  background: #000;
  cursor: pointer;
}

span.google-blue {
  color: #4285f4;
}

span.google-red {
  color: #ea4335;
}

span.google-yellow {
  color: #fbbc05;
}

span.google-green {
  color: #34a853;
}

.profile-container {
  display: flex;
  flex-direction: column;
  height: 80px;
}

.greeting {
  font-size: xx-large;
  font-weight: 500;
  text-align: center;
  margin-bottom: 15px;
}

.edit-profile {
  text-align: right;
  margin-bottom: 7px;
}

.profile-edit-btn {
  height: 25px;
  border-radius: 7px;
  color: #304ffe;
  font-weight: 500;
  width: 95px;
}
.profile-edit-btn:hover {
  background-color: #304ffe;
  color: #fff;
  transition: all 1s ease;
}

.edit-finish-btn {
  height: 25px;
  background-color: #fff;
  border-radius: 7px;
  border: none;
  width: 95px;
  margin-left: 5px;
}

.logout-container {
  text-align: right;
}

.logout-btn {
  height: 25px;
  border-radius: 7px;
  color: red;
  width: 95px;
  text-align: center;
}

.logout-btn:hover {
  background-color: red;
  color: #fff;
  transition: all 1s ease;
}

.todolist-btn {
  width: 25px;
  height: 28px;
  color: #fff;
  background-color: Transparent;
}

.cancel-btn:hover {
  color: red;
  transition: all 0.5s ease;
}
.edit-btn:hover {
  color: #000;
  transition: all 0.5s ease;
}
.todolist-row {
  height: 28px;
}
/* for Task */
.todo-table-task-column {
  padding-left: 2px;
  padding-right: 2px;
  width: 255px;
}
.todo-edit-task-box {
  width: 255px;
  text-align: center;
}
.todo-edit-task {
  border-radius: 7px;
  border: none;
  width: 255px;
}

/* for Category */
.todo-table-category-column {
  padding-left: 2px;
  padding-right: 2px;
  width: 140px;
}
.todo-edit-category-box {
  width: 140px;
  text-align: center;
}
.todo-edit-category {
  border-radius: 7px;
  border: none;
  width: 140px;
}
/* for format */
.todo-table-format-column {
  padding-left: 2px;
  padding-right: 2px;
  text-align: center;
  width: 85px;
}
.todo-edit-format-box {
  width: 85px;
  text-align: center;
}
.todo-edit-format {
  border-radius: 7px;
  border: none;
  width: 85px;
}
/* for due-date */
.todo-table-due-column {
  padding-left: 2px;
  padding-right: 2px;
  text-align: center;
  width: 60px;
}
.todo-edit-due-box {
  width: 60px;
  text-align: center;
}
.todo-edit-due {
  border-radius: 7px;
  border: none;
  width: 60px;
}
/* for study-time */
.todo-table-study-column {
  padding-left: 2px;
  padding-right: 2px;
  text-align: center;
  width: 90px;
}

.todo-title {
  font-size: xx-large;
  font-weight: 500;
  text-align: center;
  margin-bottom: 15px;
}

.todo-filter-div {
  text-align: center;
}

.todo-filter-btn {
  border-radius: 7px;
  font-weight: 500;
  font-size: medium;
  margin: 5px;
}

.todo-filter-btn:hover {
  color: #fff;
  background-color: #304ffe;
  transition: all 1s ease;
}

/* todolists table 관련 */
.todolists-table-container {
  height: 310px;
  overflow-x: hidden;
  overflow-y: overlay;
}
.fixedHeader {
  position: sticky;
  top: 15px;
}

.todolist-table {
  width: 720px;
  margin-top: 20px;
}

.todolist-thead {
  font-size: large;
  font-weight: 500;
  height: 30px;
}

.todolist-task-column {
  width: 255px;
}
.todolist-category-column {
  width: 140px;
}
.todolist-format-column {
  width: 85px;
}

.todolist-due-column {
  width: 60px;
}

.todolist-studytime-column {
  width: 90px;
}

.todo-form-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 55px;
}

.todo-assign-table {
  text-align: right;
  width: 100%;
}

.todo-assign-input {
  border-radius: 7px;
  height: 28px;
  width: 150px;
  border: none;
}
.todo-assign-column {
  font-size: larger;
  text-align: center;
  font-weight: 500;
}

.todo-assign-row {
  height: 35px;
}

.todo-assign-submit-btn {
  font-size: medium;
  width: 100px;
  height: 28px;
  margin-top: 10px;
  font-weight: 500;
  color: #304ffe;
  border-radius: 7px;
}

.todo-assign-submit-btn:hover {
  background-color: #304ffe;
  color: #fff;
  transition: all 1s ease;
}

.timer-title {
  font-size: xx-large;
  font-weight: 500;
  text-align: center;
  margin-bottom: 25px;
}

.display-time {
  font-size: 50px;
  text-align: center;
  margin-bottom: 20px;
}
/* buttons */
.timer-btns {
  text-align: center;
  margin-bottom: 15px;
}
.timer-btn {
  background-color: transparent;
  color: white;
}
.left-btn {
  margin-right: 15px;
}
.left-btn:hover {
  color: #304ffe;
  transition: all 1s ease;
}
.right-btn {
  margin-left: 15px;
}
.right-btn:hover {
  color: red;
  transition: all 1s ease;
}
/* select box */
.add-timer-container {
  text-align: center;
}

.timer-select-todolists {
  height: 25px;
  width: 200px;
  border-radius: 7px;
  border: none;
  margin-right: 5px;
}
.timer-select-btn {
  color: #304ffe;
  height: 25px;
  font-size: medium;
  font-weight: 500;
  width: 100px;
  border-radius: 7px;
  margin-right: 5px;
}
.timer-select-btn:hover {
  color: white;
  background-color: #304ffe;
  transition: all 1s ease;
}

input {
  width: 100px;
}

.home-container {
  display: flex;
  justify-content: center;
  flex-flow: row-reverse wrap;
  margin-top: 60px;
}

.first-inner-container {
  display: flex;
  flex-direction: column;
  margin: 20px;
}

.second-inner-container {
  display: flex;
  flex-direction: column;
  margin: 20px;
}

.first-box-container {
  background-color: rgb(42, 98, 255, 0.5);
  height: 310px;
  padding: 30px;
  width: 340px;
  border-radius: 20px;
  box-shadow: 3px 3px #000;
}

.second-box-container {
  background-color: rgb(42, 98, 255, 0.5);
  margin-top: 40px;
  height: 220px;
  padding: 30px;
  width: 340px;
  border-radius: 20px;
  box-shadow: 3px 3px #000;
}

.third-box-container {
  background-color: rgb(42, 98, 255, 0.5);
  height: 420px;
  width: 740px;
  padding-top: 30px;
  padding-bottom: 30px;
  padding-right: 20px;
  padding-left: 20px;
  border-radius: 20px;
  box-shadow: 3px 3px #000;
}

.fourth-box-container {
  margin-top: 40px;
  background-color: rgb(42, 98, 255, 0.5);
  height: 110px;
  padding: 30px;
  border-radius: 20px;
  box-shadow: 3px 3px #000;
}


